.back {
    background-color: #67e44d;
    background-size:cover; 
    min-height: 100vh;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items:flex-start;
    background-position: 45%;
}


.boton {
    display: inline-block;
    padding: 1px 20px;
    color: #c6f799;
    letter-spacing: 2px;
    font-size: 35px;
    text-decoration: none;
    transition: 0.5s;
    background-color: #a945c700;
    border: none;
    box-shadow: 0px 0px 10px #67e44d, 0px 0px 0px #67e44d, 0 0 10px #67e44d;
    margin-top: 275px;
    margin-right: 590px;
    font-family:fantasy;
}

.boton:hover {
    font-size: 40px;
    cursor: pointer;
}